@use '../base/mixins' as mixin;

.settings-title {
  @include mixin.small-laptop {
    display: none;
  }
}

.section-settings {
  @include mixin.tablet {
    padding-top: 0;
  }
}

.settings {
  &__tabs {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    max-width: 187px;
    gap: 5px 12px;

    @include mixin.tablet {
      flex-direction: row;
      gap: 12px;
      padding: 8px 0;
    }

    .nav-link {
      display: block;
      padding: 12px;
      border-radius: 12px;
      display: flex;
      align-items: center;
      gap: 16px;
      transition: 0.3s linear;
      cursor: pointer;

      @include mixin.tablet {
        padding: 4px 12px;
      }

      span {
        font-size: 14px;
        font-weight: 500;
        line-height: 1.5;
      }

      svg {
        @include mixin.tablet {
          display: none;
        }
      }

      path {
        transition: 0.3s linear;
      }

      &.active,
      &:hover {
        background: var(--primary-lightest, #ecf6ff);
        color: var(--primary-darker, #1874c1);

        path {
          fill: var(--primary-darker, #1874c1);
        }
      }
    }
  }

  &__tab-bodies {
    @include mixin.tablet {
      padding: 24px;
    }
  }

  &__tab-body {
    display: none;

    &.active {
      display: block;
    }

    @include mixin.tablet {
      display: none;
      justify-content: center;

      &.active {
        display: flex;
      }
    }
  }
}

.card {
  max-width: 296px;
  padding: 24px;
  border-radius: 32px;
  border: 1px solid var(--secondary-light, #eee);

  &__breadcrumb {
    margin-bottom: 16px;
    align-self: flex-start;
  }

  &__title {
    font-size: 16px;
    font-weight: 600;
    line-height: 1.2;
    margin-bottom: 24px;

    &--mb-8 {
      margin-bottom: 8px;
    }
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 12px;

    li {
      display: flex;
      align-items: center;
      gap: 12px;

      span {
        font-size: 14px;
        font-weight: 400;
        line-height: 1.5;

        a {
          font-size: 14px;
          color: var(--primary-darker, #1874c1);
          text-decoration: underline;
        }
      }
    }
  }

  &__list-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background: var(--primary-lightest, #ecf6ff);
    width: 32px;
    height: 32px;
    min-width: 32px;
  }

  p {
    margin: 0;
    margin-bottom: 8px;
  }

  &__btns {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
}